import '../index.css';
import logologin from '../assets/img/logo-list.png'
import axios from 'axios';
import { useState } from 'react';
import { getToken ,saveToken} from '../localstorage/token';
import { ToastContainer, toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

export function Login () {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [accessToken, setAccessToken] = useState('');
    const navigate = useNavigate();
  
    const handleLogin = async () => {
      try {
        const response = await axios.post('https://panel.ibrokers.ir/api/account/login/', {
          username: username,
          password: password,
        });
  
        if (response.status === 200) {
          console.log(response.data['access']);
          const token = response.data['access']; 
          saveToken(token)
          // setAccessToken(token);
          navigate('/')
          console.log(' . :', token);

        } else {
          console.log(' . :', response.status);
        }
      } catch (error) {
        console.error( error);
        toast.error('      ');
      }
    };
    return(
        <>
            <div className='login'>
                <div className='inset-login'>
                    <img src={logologin} />
                    
                    <p>   </p>

                    <label> :  </label>
                    <input 
                        className='input-login' 
                        type='text' 
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        />

                    <label> :   </label>
                    <input 
                        className='input-login' 
                        type='password' 
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        />
                    
                    <button className='btn-login' onClick={handleLogin}></button>
                </div>
            </div>
        </>
    )
}